<template>
	<div id="app">
		<header>
			<div class="head">
				<div class="logobox">
					<router-link to="/home">
						<img src="../static/laiye_img/laiye_logo_1.06aa6f8.png" class="logo" />
					</router-link>
				</div>
				<nav>
					<router-link to="/home">首页</router-link>
					<router-link to="/serve">服务</router-link>
					<router-link to="/cooperation">合作</router-link>
				</nav>
			</div>
		</header>
		<div class="boxx"></div>
		
		<div class="view_hied">
			<router-view/>
		</div>

		<footer>
			<div class="foot">
				<p><span>公司</span>北京来也网络科技有限公司</p>
				<p><span>合作</span>partner@laiye.com</p>
				<p><span>地址</span>北京海淀融科资讯中心A座701/上海徐汇乐山路33号慧谷创业1号楼205</p>
				<p>京ICP证161386号</p>
			</div>
		</footer>
	</div>
</template>
<script>
	import './assets/js/swiper.min.js'
//	import './assets/js/jquery-1.9.1.js'		
	
	export default {
		name: 'app',
		data() {
			return {
				
			}
		},
		mounted() {
			
		},
		watch: {  
		    '$route' (to, from) {  
		    	$('html,body').animate({  
				    scrollTop: 0  
				}, 0);
		    }  
  		}  
		
	}
</script>
<style>
	@import url("./assets/css/swiper.min.css");
	
	#app {width: 100%;height: auto;}
	
	* {margin: 0;padding: 0;font-family: "微软雅黑";font-size: 16px;}
	a {text-decoration: none;}
	ul,ol {list-style: none;}
	
	header {width: 100%;height: 58px;background: #fff;position: fixed;top: 0;z-index: 9999;box-shadow: 10px 0 20px rgba(0, 0, 0, .08);}
	header .head {width: 80%;height: 58px;margin: 0 auto;display: flex;justify-content: space-between;}
	header .head .logobox {width: 58px;height: 58px;}
	header .head .logobox .logo {width: 100%;height: 100%;}
	header .head nav {margin-right: 110px;}
	header .head nav a {display: inline-block;padding: 0 22px;width: 58px;height: 58px;line-height: 58px;text-align: center;font-weight: 300;color: #000;}
	header .head nav a:hover {-webkit-transition: all .3s;-o-transition: all .3s;transition: all .3s;color: #ffe660;}
	.boxx {width: 100%;height: 58px;}
	
	
	footer {width: 100%;background: #f6f6f6;}
	footer .foot {max-width: 1200px;margin: 0 auto;display: flex;align-content: center;justify-content: center;flex-wrap: wrap;line-height: 80PX;}
	footer .foot p {margin-right: 10px;}
	footer .foot p span {opacity: .4;font-size: 16px;margin-right: 12px;font-weight: 300;white-space: nowrap;}
	
	@media screen and (max-width:1200px) {
		header .head nav {margin-right: 0px;}
	}
	
	@media screen and (max-width:1158px) {
		footer .foot {line-height: 40PX;}
	}
	
	@media screen and (max-width:750px) {
		header .head nav {margin-right: 0px;}
		header .head {width: 90%;}
	}
	
	@media screen and (max-width:574px) {
		header .head {width: 100%;}
		header .head nav a {padding: 0 10px;}
		footer .foot {line-height: 20PX;}
		footer .foot p,footer .foot p span {font-size: 14px;}
	}
	
	@media screen and (max-width:375px) {
		header .head nav a {padding: 0 5px;}
		footer .foot p,footer .foot p span {font-size: 12px;}
	}
</style>


