import Vue from 'vue' //引用vue文件
import Router from 'vue-router' /* 引用vue路由模块,并复制给变量router*/
import home from '@/components/home' /*英文hello.vue模版,并赋值给hello,这里@相当于../*/
import serve from '@/components/serve'
import cooperation from '@/components/cooperation'



Vue.use(Router) /*使用路由*/

export default new Router({
	routes: [
	{ /* 进行路由配置,规定/引入到hello组件*/
		path: '/',
		name: 'home',
		/*这里name同上,暂时没发现有什么意思*/
		component: home /*注册hello组件*/
	},
	{
		path:"/home",
		name: 'home',
		component: home
	},
	{
		path:"/serve",
		name: 'serve',
		component: serve
	},
	{
		path:"/cooperation",
		name: 'cooperation',
		component: cooperation
	}]
})


